@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.2/css/all.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/4.5.0/css/bootstrap.min.css);
@import url(https://fonts.googleapis.com/css2?family=Parisienne&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&family=Roboto:wght@400;500;700&display=swap);
:root {
  --themeColor1: rgb(72, 61, 139, 0.9);
  --themeColor2: rgb(72, 61, 139, 0.1);
  --themeColor3: #f0fff0;
  --themeColor4: black;
  --themeColor5: white;
}
html {
  scroll-behavior: smooth;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}
.max-width {
  width: 1350px;
  margin: auto;
}
.logo,
.logo-2 {
  font-size: 58px;
  font-weight: 600;
  font-family: "Roboto", cursive;
  color: #f0fff0;
  color: var(--themeColor3);
}
.logo span,
.logo-2 span {
  color: rgb(72, 61, 139, 0.9);
  color: var(--themeColor1);
}
.home-content {
  border-width: 18px;
  border-color: white;
  border-color: var(--themeColor5);
  border-style: solid;
  padding: 10px;
  background-color: black;
  background-color: var(--themeColor4);
  width: 45vw;
}
button,
.contactButton {
  background: rgb(72, 61, 139, 0.9);
  background: var(--themeColor1);
  color: white;
  padding: 6px 20px;
  border-radius: 8px;
  transition: all 0.3s ease;
}
button:hover,
.contactButton:hover {
  color: rgb(72, 61, 139, 0.9);
  color: var(--themeColor1);
  border: 1px solid rgb(72, 61, 139, 0.9);
  border: 1px solid var(--themeColor1);
  background-color: rgb(72, 61, 139, 0.1);
  background-color: var(--themeColor2);
}
.contactButton {
  font-size: 60px;
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  z-index: 999;
  padding: 0 25px;
  background-color: rgb(72, 61, 139, 0.9);
  background-color: var(--themeColor1);
  color: white;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  font-weight: 600;
  padding: 8px 10px;
  transition: all 0.3s ease;
}
.header .menu {
  margin: 15px 0;
}
.header .menu li {
  list-style: none;
  display: inline-block;
  padding-right: 20px;
}
.header .menu li a {
  display: block;
  border-radius: 5px;
  padding: 5px;
  transition: color 0.3s ease;
  text-decoration: none;
  color: inherit;
}
.header .menu li a:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.home {
  display: flex;
  background: url(/markforesterreact/static/media/collaged1.92ac2a53.jpg) no-repeat center;
  height: 100vh;
  color: black;
  color: var(--themeColor4);
  background-size: cover;
  font-family: "Roboto", sans-serif;
}
.max-width {
  margin: auto 0 auto 40px;
}
.home .home-content .text-1 {
  font-size: 25px;
}

.text-2 {
  font-size: 35px;
  margin: 5px 0;
  color: #f0fff0;
  color: var(--themeColor3);
}
.text-2 span {
  color: rgb(72, 61, 139, 0.9);
  color: var(--themeColor1);
  font-weight: bolder;
}
.home .home-content button {
  display: inline-block;
  background: rgb(72, 61, 139, 0.9);
  background: var(--themeColor1);
  color: white;
  color: var(--themeColor5);
  padding: 12px 20px;
  font-size: 25px;
  margin: 20px 15px 0 0;
  border-radius: 8px;
  transition: all 0.3s ease;
}

footer {
  background-color: rgb(72, 61, 139, 0.9);
  background-color: var(--themeColor1);
  color: white;
  color: var(--themeColor5);
  text-align: center;
  font-size: 18px;
  padding: 25px 0;
  font-family: "Montserrat", sans-serif;
}

/*****************************************************************/

/* Arranging the main-section */

.main-section {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: black;
  background-color: var(--themeColor4);
  position: relative;
  padding-top: 1vh;
  padding-left: 1vw;
  border: 3px ridge black;
  border: 3px ridge var(--themeColor4);
}

/*****************************************************************/
/*welcome-section styling*/
.about-me,
li,
.contact-me {
  font-size: 150%;
}
ul {
  list-style-type: disc;
  margin-left: 50px;
}

ul li {
  color: white;
  color: var(--themeColor5);
  margin-left: 50px;
}

.separator {
  width: 120%;
}

/*****************************************************************/

.box-2 {
  border-radius: 5px;
  align-content: center;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-bottom: 20px;
  padding-top: 20px;
}
.box-2 .card {
  width: 500px;
  height: 700px;
  padding: 15px;
  border-radius: 8px;
  align-items: flex-start;
  background-color: #f0fff0;
  background-color: var(--themeColor3);
  cursor: pointer;
  overflow-y: scroll;
  transition: all 0.3s ease;
}
::-webkit-scrollbar {
  width: 5px;
}

.box-2 .card:hover {
  -webkit-animation-duration: 0.75s;
          animation-duration: 0.75s;
  -webkit-animation-name: glow-black;
          animation-name: glow-black;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
/***********************************************/
/* Profile link image */
.profile-link-image {
  width: 60px;
  height: 60px;
}

.contact-btn {
  width: 120px;
  height: 120px;
}
.card-columns {
  -webkit-column-count: 3;
          column-count: 3;
}
/*****************************************************************/
/* Responsive styling */

/* for smaller tablets (iPad Pro)*/
@media screen and (max-width: 1350px) {
  .nav-link {
    font-size: 75%;
  }
  .contact-me,
  .about-me,
  li,
  .project-tile h2 {
    font-size: 100%;
  }
  .project-note {
    font-size: 60%;
  }
  ul {
    margin-left: 3vw;
  }
  h1 {
    font-size: 200%;
  }
  .card-columns {
    -webkit-column-count: 1;
            column-count: 1;
  }
  .card {
    width: 50vw;
  }
}

@media screen and (max-width: 768px) {
  .nav-link {
    font-size: 75%;
  }
  .contact-me,
  .about-me,
  li,
  .project-tile h2 {
    font-size: 100%;
  }
  .project-note {
    font-size: 60%;
  }
  ul {
    margin-left: 3vw;
  }
  h1 {
    font-size: 200%;
  }
  .home {
    background-image: none;
    background-color: black;
    background-color: var(--themeColor4);
  }
  .card-columns {
    -webkit-column-count: 1;
            column-count: 1;
  }
  .card {
    width: 75vw;
  }
}

/*for phones*/

@media screen and (max-width: 600px) {
  .home-content {
    margin-left: -10vw;
    padding: 1vw;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: small;
    align-self: center;
  }

  .nav-link {
    font-size: 75%;
  }
  .contact-me,
  .about-me,
  li,
  .project-tile h2 {
    font-size: 100%;
  }
  .project-note {
    font-size: 60%;
  }
  ul {
    margin-left: 3vw;
  }
  h1 {
    font-size: 150%;
  }
  .home {
    background-image: none;
    background-color: black;
    background-color: var(--themeColor4);
  }
  .card-columns {
    -webkit-column-count: 1;
            column-count: 1;
  }
  .card {
    width: 95vw;
  }
}

/*the glow effect on the borders*/
@-webkit-keyframes glow {
  0% {
    box-shadow: 1.75px 1.75px 20px 1.25px rgba(255, 6, 255, 0.1);
  }
  50% {
    box-shadow: 1.75px 1.75px 20px 1.25px rgba(195, 0, 255, 0.75);
  }
  100% {
    box-shadow: 1.75px 1.75px 20px 1.25px rgb(255, 0, 212);
  }
}
@keyframes glow {
  0% {
    box-shadow: 1.75px 1.75px 20px 1.25px rgba(255, 6, 255, 0.1);
  }
  50% {
    box-shadow: 1.75px 1.75px 20px 1.25px rgba(195, 0, 255, 0.75);
  }
  100% {
    box-shadow: 1.75px 1.75px 20px 1.25px rgb(255, 0, 212);
  }
}

@-webkit-keyframes glow-black {
  0% {
    box-shadow: 1.75px 1.75px 20px 1.25px rgba(255, 255, 255, 0.1);
  }
  50% {
    box-shadow: 1.75px 1.75px 20px 1.25px rgba(175, 27, 175, 0.75);
  }
  100% {
    box-shadow: 1.75px 1.75px 20px 1.25px rgb(255, 0, 255);
  }
}

@keyframes glow-black {
  0% {
    box-shadow: 1.75px 1.75px 20px 1.25px rgba(255, 255, 255, 0.1);
  }
  50% {
    box-shadow: 1.75px 1.75px 20px 1.25px rgba(175, 27, 175, 0.75);
  }
  100% {
    box-shadow: 1.75px 1.75px 20px 1.25px rgb(255, 0, 255);
  }
}

/*End Styling*/
